/* 
  Quotes
  =======================================
  Blockquotes etc
  ======================================= */
.pullquote {
  position: relative;
  padding: 2.5rem 0;
  text-align: center; }
  .pullquote::before, .pullquote::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(285.1deg, rgba(158, 69, 147, 0) -15.62%, #9e4593 146.86%);
    width: 7.8125rem;
    height: 0.3125rem; }
  .pullquote::before {
    top: 0; }
  .pullquote::after {
    bottom: 0; }
  @media (min-width: 48em) {
    .pullquote {
      text-align: left;
      /*content:"768px";*/ }
      .pullquote::before, .pullquote::after {
        left: 0;
        transform: translateX(0); } }
  .pullquote p {
    font-weight: 900;
    font-size: 1.5625rem;
    line-height: 2.5rem;
    text-transform: uppercase;
    padding-bottom: 1.25rem; }
    @media (min-width: 48em) {
      .pullquote p {
        font-weight: 900;
        font-size: 1.9375rem;
        line-height: 2.75rem;
        /*content:"768px";*/ } }
  .pullquote cite {
    font-weight: 900;
    font-size: 0.75rem;
    line-height: 0.75rem;
    letter-spacing: 0.15rem;
    text-transform: uppercase;
    font-style: normal; }

.wysiwyg-typography {
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 2.875rem;
  color: #ffffff; }
  .wysiwyg-typography p,
  .wysiwyg-typography li,
  .wysiwyg-typography p span {
    color: #ffffff;
    font-weight: 400;
    font-size: 1.125rem;
    line-height: 1.9375rem; }
    @media (min-width: 48em) {
      .wysiwyg-typography p,
      .wysiwyg-typography li,
      .wysiwyg-typography p span {
        font-weight: 500;
        font-size: 1.5rem;
        line-height: 2.875rem;
        /*content:"768px";*/ } }
  .wysiwyg-typography ul,
  .wysiwyg-typography ol {
    margin-left: 1em; }
  .wysiwyg-typography h1,
  .wysiwyg-typography h2 {
    color: #ffffff;
